.list {
  margin-bottom: 10px;
  line-height: 40px;
  border-bottom:1px solid #ccc;
  list-style: none;
  .content-top{
    display: flex;
  justify-content: space-between;
    font-size: 16px;
    .title{
      width: 200px;
    }
    .left{
      display: flex;
      align-items: center;
    }
    .link{
      display: block;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 400px;
      font-weight: bold;
    }
  }
  .content-bottom {
    padding-left: 50px;
    display: flex;
  justify-content: space-between;
    width: 340px;
    .title{
      width: 80px;
    }
    .title-end{
      width: 160px;
    }
    span{
      // margin-right: 10px;
      // width: 100px;
    }
  }
  img{
    width: 40px;
    height: 40px;
    margin-right: 10px;
    border-radius: 50%;
  }
  a{
    color:#000;
  }
}
a:hover{
    text-decoration: underline;
    color:#108ee9;
}